import { createRouter, createWebHistory } from 'vue-router'
import JoMalone from '../views/JoMalone.vue'
import Story from '../views/story.vue'
import ScentPairing from '../views/Scent Pairing.vue'
import EPFChronic from '../views/EPF-Chronic.vue'
import EPFLayering from '../views/EPF-Layering.vue'
import SeaDaffodil from '../views/Sea-Daffodil.vue'
import SignUpView from '../views/SignUpView.vue'
import LoginView from '../views/LoginView.vue'
import CartView from '../views/CartView.vue'



const routes = [
  {
    path: '/',
    name: 'JoMalone',
    component: JoMalone
  },
  {
    path: '/story',
    name: 'story',
    component: Story
  },
  {
    path: '/Scent-Pairing',
    name: 'Scent-Pairing',
    component: ScentPairing
  },
  {
    path: '/EPF-Chronic',
    name: 'EPF-Chronic',
    component: EPFChronic
  },
  {
    path: '/EPF-Layering',
    name: 'EPF-Layering',
    component: EPFLayering
  },
  {
    path: '/SeaDaffodil',
    name: 'SeaDaffodil',
    component: SeaDaffodil
  },
  {
    path: '/SignUpView',
    name: 'SignUpView',
    component: SignUpView
  },
  {
    path: '/LoginView',
    name: 'LoginView',
    component: LoginView
  },
  {
    path: '/cart',
    name: 'cart',
    component: CartView
  },

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
