<template>

  <div class="container-fluid py-5 ">
    <div class="row text-center">
      <figure>
        <img class="login-pic" src="../assets/images/login-pic.jpg" alt="">
      </figure>
      <form action="">
        <div class="login ">
          <div class="login-opacity">
            <h1>登入</h1>
            <input type="mail" placeholder="帳號" size="30" v-model="email">
            <br>
            <input type="password" placeholder="密碼" size="30" v-model="password">
            <br> <br>
            <button class="member-btn" type="submit" @click.prevent="doLogin">登入</button>
          </div>
          <br>
        </div>
        <router-link to="/SignUpView" class="join-mb">加入會員</router-link>
      </form>
    </div>
  </div>

</template>

<style>
.login-pic {
  width: 100%;

}



.join-mb {
  text-decoration: none;
  color: black;
}

.join-mb:hover {
  text-decoration: underline;
  color: black;
}
</style>

<script>
export default {
  name: 'sign-up',
  data() {
    return {
      displayName: '',
      email: '',
      password: ''
    }
  },
  methods: {
    doLogin() {
      const submitForm = {
        email: this.email,
        password: this.password
      }      
        this.axios.post('/login', submitForm).then((res) => {
          console.log(res)

          this.$router.push('/')
        })
      }
    }
  }  
</script>