<template>
  <div class="container-fluid py-5">
    <div class="row text-center">
      <figure>
        <img class="login-pic" src="../assets/images/login-pic.jpg" alt="">
      </figure>
      <form action="" was-validated>
        <div class="login">
          <div class="login-opacity ">
            <div>
              <h1>註冊</h1>
              <input type="text" placeholder="姓名" v-model="displayName" size="30">
              <br>
              <input type="text" placeholder="帳號" v-model="email" size="30">
              <br>
              <input type="password" placeholder="密碼" v-model="password" size="30">
              <br><br>
              <button class="member-btn" type="submit" @click.prevent="doSignUp">送出</button>
            </div>
          </div>
        </div>
      </form>
    </div>

  </div>
</template>
<style>
</style>

<script>
export default {
  name: 'sign-up',
  data() {
    return {
      displayName: '',
      email: '',
      password: ''
    }
  },
  methods: {
    doSignUp() {
      const submitForm = {
        displayName: this.displayName,
        email: this.email,
        password: this.password
      }
      this.axios.post('/signup', submitForm).then((res) => {
        console.log(res)
        this.$router.push('/LoginView')

      })
    }
  }
}  
</script>